.taskbar-container {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-align-content: stretch;
  -ms-flex-line-pack: stretch;
  align-content: stretch;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
  width: 100%;
  background-color: #c0c0c0;
  border-top: 1px solid white;
  position:fixed;
  bottom: 0;
  -webkit-box-shadow: 0px 0px 0px 0px black, 0px 0px 0px 0px black, 0px 0px 0px 0px white, 0px 0px 0px 1px #dfdfdf, 0px 0px 0px 1px rgba(0,0,0,0);
  box-shadow: 0px 0px 0px 0px black, 0px 0px 0px 0px black, 0px 0px 0px 0px white, 0px 0px 0px 1px #dfdfdf, 0px 0px 0px 1px rgba(0,0,0,0);
}
