.start-container {
  display: -ms-inline-flexbox;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-align-content: flex-start;
  -ms-flex-line-pack: start;
  align-content: flex-start;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  border-top: 1px solid #dfdfdf;
  border-left: 1px solid #dfdfdf;
  border-right: 1px solid #808080;
  border-bottom: 1px solid #808080;
  -webkit-box-shadow: 1px 0px 0px 0px black, 0px 1px 0px 0px black, -1px 0px 0px 0px white, 0px 0px 0px 1px white, 0px 0px 0px 1px rgba(0,0,0,0);
  box-shadow: 1px 0px 0px 0px black, 0px 1px 0px 0px black, -1px 0px 0px 0px white, 0px 0px 0px 1px white, 0px 0px 0px 1px rgba(0,0,0,0);
  margin: 5px;
  padding: 4px 5px 0px 4px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  .logo-container {
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    .logo {
      width: 24px;
    }
  }
  .text-container {
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    p {
      font-size: 18px;
      font-weight: 600;
      margin: -2px 0px 0px 3px;
    }
  }
}
.StartMenu-container {
  position: absolute;
  background: #c0c0c0;
  height: 250px;
  width: 200px;
  bottom: 38px;
  left: 5px;
  border-top: 1px solid #dfdfdf;
  border-left: 1px solid #dfdfdf;
  border-right: 1px solid #808080;
  border-bottom: 1px solid #808080;
  -webkit-box-shadow: 1px 0px 0px 0px black, 0px 1px 0px 0px black, -1px 0px 0px 0px white, 0px 0px 0px 1px white, 0px 0px 0px 1px rgba(0,0,0,0);
  box-shadow: 1px 0px 0px 0px black, 0px 1px 0px 0px black, -1px 0px 0px 0px white, 0px 0px 0px 1px white, 0px 0px 0px 1px rgba(0,0,0,0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-align-content: flex-start;
  -ms-flex-line-pack: start;
  align-content: flex-start;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
   .brand-bar {
      -webkit-order: 0;
      -ms-flex-order: 0;
      order: 0;
      -webkit-flex: 0 1 10%;
      -ms-flex: 0 1 10%;
      flex: 0 1 10%;
      -webkit-align-self: stretch;
      -ms-flex-item-align: stretch;
      align-self: stretch;
      background-color:#87888f;
      align-items: flex-end;
      display: flex;
      justify-content: center;
      padding: 5px 2px;
   }
   .contents-bar {
      -webkit-order: 0;
      -ms-flex-order: 0;
      order: 0;
      -webkit-flex: 0 1 90%;
      -ms-flex: 0 1 90%;
      flex: 0 1 90%;
      -webkit-align-self: stretch;
      -ms-flex-item-align: stretch;
      align-self: stretch;
      align-items: flex-end;
      display: flex;
      -webkit-flex-direction: column-reverse;
      -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
      .list-item {
        display: flex;
        align-items: center;
        width: 100%;
        cursor: pointer;
        img {
          padding: 5px 10px;
        }
        &:hover {
          background-color: #00007f;
          color: #d4d4d4;
        }
      }
   }
}
.hidden {
   display:none;
}
.show {
   display:flex;
}
.active {
  border-top: 1px solid #808080;
  border-left: 1px solid #808080;
  border-right: 1px solid #dfdfdf;
  border-bottom: 1px solid #dfdfdf;
  -webkit-box-shadow: 1px 0px 0px 0px white, 0px 1px 0px 0px white, -1px 0px 0px 0px black, 0px 0px 0px 1px black, 0px 0px 0px 1px rgba(0,0,0,0);
  box-shadow: 1px 0px 0px 0px white, 0px 1px 0px 0px white, -1px 0px 0px 0px black, 0px 0px 0px 1px black, 0px 0px 0px 1px rgba(0,0,0,0);
  margin: 5px;
  padding: 4px 3px 0px 5px;
  &> .logo-container, .text-container {
    margin-bottom: -2px;
    marign-left:5px;
    padding-top: 2px;
  }
}
hr {
  width: 95%;
}
