.react-draggable {
  display: flex !important;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-align-content: flex-start;
  -ms-flex-line-pack: start;
  align-content: flex-start;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
  background-color: #c0c0c0;
  border-top: 1px solid #dfdfdf;
  border-left: 1px solid #dfdfdf;
  border-right: 1px solid #808080;
  border-bottom: 1px solid #808080;
  -webkit-box-shadow: 1px 0px 0px 0px black, 0px 1px 0px 0px black, -1px 0px 0px 0px white, 0px 0px 0px 1px white, 0px 0px 0px 1px rgba(0,0,0,0);
  box-shadow: 1px 0px 0px 0px black, 0px 1px 0px 0px black, -1px 0px 0px 0px white, 0px 0px 0px 1px white, 0px 0px 0px 1px rgba(0,0,0,0);
  padding: 2px;
  user-select: none;
  .header {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-align-content: flex-start;
    -ms-flex-line-pack: start;
    align-content: flex-start;
    -webkit-align-items: flex-end;
    -ms-flex-align: end;
    align-items: flex-end;
    background-color: #00007f;
    color: #c0c0c0;
    padding: 4px;
    font-size: 14px;
    -webkit-align-self: stretch;
    -ms-flex-item-align: stretch;
    align-self: stretch;
    cursor: default;
  }
  .menu {
    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
      overflow: hidden;
      display: inline-flex;
      -webkit-align-self: stretch;
      -ms-flex-item-align: stretch;
      align-self: stretch;
      li {
        padding: 4px;
        font-size: 14px;
        cursor: pointer;
      }
    }
  }
  .explorer {
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: stretch;
    -ms-flex-item-align: stretch;
    align-self: stretch;
    -webkit-flex: 0 1 100%;
    -ms-flex: 0 1 100%;
    flex: 0 1 100%;
    background-color: white;
    border-top: 1px solid #808080;
    border-left: 1px solid #808080;
    border-right: 1px solid #dfdfdf;
    border-bottom: 1px solid #dfdfdf;
    -webkit-box-shadow: 1px 0px 0px 0px white, 0px 1px 0px 0px white, -1px 0px 0px 0px black, 0px 0px 0px 1px black, 0px 0px 0px 1px rgba(0,0,0,0);
    box-shadow: 1px 0px 0px 0px white, 0px 1px 0px 0px white, -1px 0px 0px 0px black, 0px 0px 0px 1px black, 0px 0px 0px 1px rgba(0,0,0,0);
  }
  .footer {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-align-content: flex-start;
    -ms-flex-line-pack: start;
    align-content: flex-start;
    -webkit-align-items: flex-start;
    -ms-flex-align: start;
    align-items: flex-start;
    width:100%;
    margin: 5px 0px 0px 0px;
    font-size: 10px;
    .selected {
      -webkit-order: 0;
      -ms-flex-order: 0;
      order: 0;
      -webkit-flex: 0 1 33%;
      -ms-flex: 0 1 33%;
      flex: 0 1 33%;
      -webkit-align-self: center;
      -ms-flex-item-align: center;
      align-self: center;
      border-top: 1px solid #808080;
      border-left: 1px solid #808080;
      border-right: 1px solid white;
      border-bottom: 1px solid white;
      padding: 2px;
    }
    .info {
      -webkit-order: 0;
      -ms-flex-order: 0;
      order: 0;
      -webkit-flex: 0 1 67%;
      -ms-flex: 0 1 67%;
      flex: 0 1 67%;
      -webkit-align-self: center;
      -ms-flex-item-align: center;
      align-self: center;
      border-top: 1px solid #808080;
      border-left: 1px solid #808080;
      border-right: 1px solid white;
      border-bottom: 1px solid white;
      padding: 2px
    }
  }
}
